import React, { createRef, useRef, useEffect } from "react";
import { Router, Link, Location } from "@reach/router";
import { TransitionGroup, CSSTransition } from "react-transition-group";
import SEO from "../components/seo";
import "../components/layout.css";
import ContentPage from '../components/contentPage';

const App = (props) => {
  let [page, route] = [null, null];

  // If route is provided, set page and route
  const regex = /[/]+/;
  if (props.location.href.split(regex).length === 4) 
    [page, route] = props.location.href.split(regex).reverse();
  else if (props.location.href === 'http://localhost:8000/') 
    [page, route] = ['1', 'about'];
  else
    [page, route] = ['0', 'Not Found'];


  // Get total pages based on route
  const pages = { about: 2, works: 1, education: 1 }[route];
  
  // References to animated elements
  const refs = useRef(createRef());

  // Animation to change page
  const changePage = () => {
    refs.current.current.animate(
      [
        { transform: 'rotate(0deg)', top: '0vh'  },
        { transform: 'rotate(30deg)', top: '100vh' },
        { transform: 'rotate(0deg)', top: '0vh'  }
      ], 400
    );
  }

  // Upgraded link for page change animation + selected
  const NavLink = props => (
    <Link
      {...props}
      getProps={({ href }) => href.includes(route) ? { className: "selected" } : null}
      onClick={()=>changePage()}
    />
  );

  // Scroll Functions
  useEffect(() => {
    window.addEventListener('wheel', handleScroll);
    return () => {
      window.removeEventListener('wheel', handleScroll);
    };
  });

  const handleScroll = (e) =>{
    if (e.deltaY > 0) editPage('inc');
    else editPage('dec');
  }

  const editPage = (arg) => {
    const abbr = { inc: 1, dec: -1 };

    const handler = {
      get: (obj, prop) => {
        return prop in obj ? obj[prop] + parseInt(page) : prop
      }
    };

    const getPageNumber = new Proxy(abbr, handler);

    if(1<=getPageNumber[arg]&getPageNumber[arg]<=pages) {
      props.navigate(`${route}/${getPageNumber[arg]}`);
      changePage();
    }
  }

  const getNav = () => {
    return(
      <nav id="content__navigation">
        <div onClick={() => editPage('dec')} id="content__navigation__dec">^</div>
        {
          Array.from({ length: pages }, (v, i) => i + 1).map(index => {
            return(<span onClick={()=>editPage(index)} style = {index == page ? {'backgroundColor': 'white'} : {}} className="content__navigation__item" for={"page" + index}/>)
          })
        }
        <div onClick={() => editPage('inc')} id="content__navigation__inc">^</div>
      </nav>
    );
  }

  return (
    <main id="app">
      <SEO title={`${route.charAt(0).toUpperCase()}${route.slice(1)} ${page}/${pages}`} />
      <section id="header">
        <aside id="note">
          <strong>Welcome to my portfolio</strong>
          <div style={{marginTop: '1.5vmin'}}>Email: austinnguyen01@gmail.com</div>
          <div>Discord: KyneticDerpcake#2878</div>
        </aside>
        <section id="header__title">
          <div id="header__title__first">Austin</div>
          <div id="header__title__last">Nguyen
            <div id="header__title__number">01</div>
          </div>
        </section>
        <nav id="header__navigation">
          <NavLink to="about/1">About</NavLink>
          <NavLink to="works/1">Works</NavLink>
          <NavLink to="education/1">Education</NavLink>
        </nav>
      </section>
      <article id="content">
        {getNav()}
        <section id="content__grid">
          <svg id="content__grid__folder--top" viewBox="0 0 3940 100">
            <path fill="#f2d187" d="M0,0 L0,100 L 3940,100 L 3940,0 L0,100 Z"></path>
          </svg>
          <div id="content__grid__page">
            <section ref={refs.current}>
              <Location>
                {({ location }) => (
                  <TransitionGroup>
                    <CSSTransition
                      key={location.key}
                      classNames="delay"
                      timeout={200}
                    >
                      <Router>
                        {/* using * instead of path names for the animation*/}
                        <ContentPage path="*/*" page={page} route={route} pages={pages}/>
                      </Router>
                    </CSSTransition>
                  </TransitionGroup>
                )}
              </Location>
            </section>
          </div>
          <svg id="content__grid__folder--left" viewBox="0 0 100 2000">
            <path fill="#edbf59" d="M0,0 L100,100 L 100,2000 L 0,2000 Z"></path>
          </svg>
        </section>
      </article>
    </main>
  );
}

export default App;
