import React from "react";
import Loadable from 'react-loadable';

// Set up our routes
const About = Loadable({
    loader: () => import('./contentPages/about'),
    loading() {
        return <div>Loading...</div>
    }
});

const Education = Loadable({
    loader: () => import('./contentPages/education'),
    loading() {
        return <div>Loading...</div>
    }
});

const Works = Loadable({
    loader: () => import('./contentPages/works'),
    loading() {
        return <div>Loading...</div>
    }
});

const NotFound = Loadable({
    loader: () => import('./contentPages/notFound'),
    loading() {
        return <div>Loading...</div>
    }
});

// Proxy to determine if the page exist or not
const pages = (route, page) => ({
    about: <About page={page}/>,
    works: <Works page={page}/>,
    education: <Education page={page}/>
}[route]);

const handler = {
    apply: (pages, route, page) => {
        const searchedPage = pages.apply(route, page);
        return searchedPage ? searchedPage : <NotFound />;
    }
}
const pageProxy = new Proxy(pages, handler);

export default (props) => {
    return (
        <div>
            <div id="content__grid__page__header" >
                <span>{props.route}</span>
                <span id="content__grid__page__header__page">{props.page}/{props.pages}</span>
            </div>
            {pageProxy(props.route, props.page)}
        </div>
    );
}
  